.homepage-container {
  max-width: 700px;
  margin: 60px auto;
  padding: 2rem;
  background: #fff;
  border-radius: 12px;
  box-shadow: 0 2px 12px rgba(0,0,0,0.08);
  text-align: center;
}

.homepage-container h1 {
  color: #8A2BE2;
  margin-bottom: 1rem;
}

.homepage-container p {
  color: #444;
  font-size: 1.2rem;
  margin-bottom: 2rem;
}

.homepage-cta {
  display: inline-block;
  background: #8A2BE2;
  color: #fff;
  padding: 0.75rem 2rem;
  border-radius: 6px;
  text-decoration: none;
  font-weight: bold;
  font-size: 1.1rem;
  transition: background 0.2s;
}

.homepage-cta:hover {
  background: #54008b;
} 